.block{
	position: absolute;width: 100%;height: 100vh;top: 0;left: 0;
	z-index: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(0, 0, 0, 0.6);
	transition: all 0.3s ease 0s;
}
.fetching{
	width: 200px;
	height: 200px;
	display: block;
	margin: 0 auto;
}